import { Form, InputNumber, Tooltip } from "antd";
import React, {useState} from 'react';

const Glory = () => {
  const [building, setBuilding] = useState({
    fire: 0,
    spd: 0,
    atk: 0,
    hp: 0,
    def: 0,
    criDmg: 0,
  });
  const updateField = e => {
    console.log("e.target", e, e.target);
    setBuilding(
      {
        ...building,
        [e.target.name]: e.target.value
      },
      console.log("실행되네")
    );
  };
  return (
    <div className="glory">
      <p>(Glory building) 명건작</p>
      <Form
        labelCol={{ span: 4 }}
        wrapperCol={{ span: 14 }}
        layout="horizontal">
        <Form.Item label="Fire Building">
          <Form.Item name="fire" onChange={updateField}>
            <Tooltip placement="topLeft" title="불속성 공격력 건물">
              <InputNumber name="fire" placeholder="불속성 공격력 건물" max={10}/>
            </Tooltip>
          </Form.Item>
        </Form.Item>
        <Form.Item label="SPD">
          <Form.Item name="spd" onChange={updateField}>
            <Tooltip placement="topLeft" title="공속건물">
              <InputNumber name="spd" placeholder="공속" max={10} />
            </Tooltip>
          </Form.Item>
        </Form.Item>
        <Form.Item label="ATK">
          <Form.Item name="atk" onChange={updateField}>
            <Tooltip placement="topLeft" title="공격력 건물">
              <InputNumber name="atk" placeholder="공격력" max={10}/>
            </Tooltip>
          </Form.Item>
        </Form.Item>
        <Form.Item label="HP">
          <Form.Item name="atk" onChange={updateField}>
            <Tooltip placement="topLeft" title="체력 건물">
              <InputNumber name="hp" placeholder="체력" max={10}/>
            </Tooltip>
          </Form.Item>
        </Form.Item>
        <Form.Item label="DEF">
          <Form.Item name="def" onChange={updateField}>
            <Tooltip placement="topLeft" title="방어력 건물">
              <InputNumber name="def" placeholder="방어력" max={10} />
            </Tooltip>
          </Form.Item>
        </Form.Item>
        <Form.Item label="CRI_DMG">
          <Form.Item name="criDmg" onChange={updateField}>
            <Tooltip placement="topLeft" title="치피 건물">
              <InputNumber name="criDmg" placeholder="치피" max={10}/>
            </Tooltip>
          </Form.Item>
        </Form.Item>
      </Form>
    </div>
  );
};

export default Glory;
